import { Button, Grid, TextField } from '@mui/material'
import axios from 'axios'
import React, { useState } from 'react'
import { Link } from 'react-router-dom'

export default function CreatePost() {
  const [name, setName] = useState('')
  const [date, setDate] = useState('')
  const [title, setTitle] = useState('') 
  const [blog, setBlog] = useState('')

  const handle = () => {

    axios
      .post('/blog/send', {
        name,
        date,
        title,
        blog
      })
      .then((pre) => {
        console.log(pre, "postingdata")
      })
      .catch((error) => {
        console.log(error)
      })
  }

  return (
    <div>
      <Grid container>
    <Grid xs={12}><TextField variant='outlined' value={name} onChange={(e) => setName(e.target.value)} placeholder='Name' /></Grid>  
    <Grid xs={12}><TextField variant='outlined' value={date} onChange={(e) => setDate(e.target.value)} placeholder='Date' /></Grid>  
    <Grid xs={12}><TextField variant='outlined' value={title} onChange={(e) => setTitle(e.target.value)} placeholder='Tilte'/></Grid>  
    <Grid xs={12}><TextField variant='outlined' value={blog} onChange={(e) => setBlog(e.target.value)} placeholder='Blog' fullWidth/></Grid>  
      </Grid>
      <Button variant='outlined' onClick={(e) => handle(e)}>Post</Button>
    <Link to='/update'><Button variant='outlined'>Update</Button></Link> 
    <Link to='/delete'><Button variant='outlined'>Delete</Button></Link> 
    </div>
  )
}
