import { AppBar, Button, Card, Grid, Paper, TextField, } from '@mui/material'
import axios from 'axios'
import React, { useEffect, useState } from 'react'
import CreatePost from '../CreatePost/CreatePost'
export default function Header() {
    const [data, setData] = useState([])
    const [value, setValue] = useState('')
    const [search, setSearch] = useState('')
   

    useEffect(()=>{
        fetchdata()
     },[])

         const fetchdata = async()=>{
           await  axios 
                 .get('/blog/save')
                  .then((pre)=>{
                    setData(pre.data.data)
                    setSearch(pre.data.data)
                  })
                  .catch((error)=>{
                    console.log(error)
                  })
         }

         const handleChange = (e)=>{
            if(e.target.value == ""){
                setData(search)
            }else{
                const filterResult = data.filter(item=>item.name.toLowerCase().includes(e.target.value.toLowerCase()))
                setData(filterResult)
            }
             setValue(e.target.value)
         }

    return (
        <div>
        <Paper elevation={3}>
            <AppBar position='static' color='inherit'>
                <Grid container>
                    <Grid xs={3} px={5} fontWeight='bold' fontSize={27} margin={1}>
                        Blog Application
                    </Grid>
                    <Grid xs={5} padding={1}>
                         <TextField variant='outlined' value={value} onChange={(e)=>handleChange(e)} fullWidth />
                    </Grid>
                </Grid>
            </AppBar>
        </Paper>
        <br/><br/>
        <CreatePost/>
        <br/>
            {data.map((item)=>{
                return(
                    <div>
                   <Card>
                    <ul>
                    <li>Name:{item.name}</li>
                    <li>Date:{item.date}</li>
                    <li>Title:{item.title}</li>
                    <li>Blog:{item.blog}</li>
                    </ul>
                     </Card>
                    </div>
                )
            })}
        </div>
    )
}
