import { Button, Grid, TextField } from '@mui/material'
import axios from 'axios'
import React, { useState } from 'react'
import { Link, useParams } from 'react-router-dom'

export default function Update() {
  const [name, setName] = useState('')
  const [date, setDate] = useState('')
  const [title, setTitle] = useState('') 
  const [blog, setBlog] = useState('')
  const [id, setId] = useState('')
  // const params  = useParams()
  // const {id} = params
//   console.log(id,'param')

  const handle = () => {

    axios
      .put('/update/:id', {
        name,
        date,
        title,
        // id,
        blog
      })
      .then((pre) => {
        console.log(pre, "postingdata")
      })
      .catch((error) => {
        console.log(error)
      })
  }

  return (
    <div>
      <Grid container>
    <Grid xs={12}><TextField variant='outlined' value={name} onChange={(e) => setName(e.target.value)} placeholder='Name'/></Grid>  
    <Grid xs={12}><TextField variant='outlined' value={date} onChange={(e) => setDate(e.target.value)} placeholder='Date'/></Grid>  
    <Grid xs={12}><TextField variant='outlined' value={title} onChange={(e) => setTitle(e.target.value)} placeholder='Title'/></Grid>
    <Grid xs={12}><TextField variant='outlined' value={id} onChange={(e) => setId(e.target.value)} placeholder='Id'/></Grid>    
    <Grid xs={12}><TextField variant='outlined' value={blog} onChange={(e) => setBlog(e.target.value)} placeholder='Blog' fullWidth/></Grid>
      </Grid>
      <Button variant='outlined' onClick={(e) => handle(e)}>Update</Button>
     <Link to='/'><Button variant='outlined'>Home</Button></Link>
    </div>
  )
}
